h1{
  font-style: oblique;
  text-align: center;
  font-size: 50px;
  color: antiquewhite;
}
p{
  text-align: end;
  color: aliceblue;
}


.App {
    display:flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: linear-gradient(to right,rgb(255, 255, 255),rgb(169, 99, 250));
  }
  
  .calculator {
    border-radius: 0;
    
    padding: 50px;
    background-image:linear-gradient(to right,rgb(153, 0, 255) ,rgb(0, 0, 0)) ;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }
  
  .display {
    font-size: 30px;
    margin-bottom: 10px;
    padding: 20px;
    border: 1px solid #9e9191;
    border-radius: 5px;
    background-image: linear-gradient(to right,rgba(255, 255, 255, 0.493),rgb(255, 255, 255));
  }
  
  .buttons {
    display: grid;
    grid-template-columns: repeat(4, 2fr);
    grid-gap: 40px;
    
  }
